@media all and (max-width: 1000px) and (min-width: 450px) {
  /* mobile-devices */
  .box {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media all and (max-width: 449px) and (min-width: 300px) {
  /* mobile-devices */
  .box {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media all and (max-width: 299px) and (min-width: 100px) {
  /* mobile-devices */
  .box {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
